"use client";
import { useState, useEffect, useCallback } from "react";
import { useSession } from "@/lib/auth-client";
import { Button } from "@/components/ui/button";
// Étendre l'interface Tech pour inclure les propriétés supplémentaires
interface Tech {
  id: string;
  name: string;
  technologyId: string;
  color: string;
  category: string;
  gridCols?: number;
  gridRows?: number;
  isProject?: boolean;
  favicon?: string;
  url?: string;
  description?: string;
  order?: number;
  stars?: number;
  forks?: number;
  mrr?: number;
  mrrCurrency?: string;
  isStripeCard?: boolean;
  logo?: string;
}
import { allTechnologies } from "@/components/tech-stack/tech-data";
import Image from "next/image";
import { ExternalLink, RefreshCw, CreditCard, Share2 } from "lucide-react";
import { GlowingEffect } from "@/components/ui/glowing-effect";
import GitHubLogo, { Stripe } from "@/components/logo-card";
import { useParams } from "next/navigation";
import { LoadingSkeleton } from "@/components/ui/skeleton";

const STRIPE_CARD_ID = "internal_stripe_card";

// --- Interfaces pour les données du profil ---
interface ProfileUser {
  id: string;
  name: string | null;
  username: string | null;
  email: string | null;
  image: string | null;
  description: string | null;
  createdAt: string | null;
  hasStripeConnection?: boolean;
  shareCount?: number;
}

export interface UserStack {
  id: number;
  technologies: Tech[];
}

interface ProfileData {
  user: ProfileUser;
  stack: UserStack | null;
  hasStripeConnection: boolean;
  stripeAccountMrr: { total: number; currency: string } | null;
}

export default function UserProfilePage() {
  const { username } = useParams();
  const { data: session } = useSession();
  const [profileData, setProfileData] = useState<ProfileData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [isSharing, setIsSharing] = useState(false);

  // Fonction pour hydrater les technologies avec les données complètes
  const hydrateTechnologies = useCallback((technologies: Tech[]) => {
    return technologies.map((tech) => {
      // Pour la carte Stripe, on ne cherche pas dans allTechnologies
      if (tech.technologyId === STRIPE_CARD_ID) {
        return tech;
      }

      const fullTechData = allTechnologies.find(
        (t) => t.id === tech.technologyId
      );

      if (!fullTechData) return tech;

      return {
        ...tech,
        color: tech.color || fullTechData.color,
        category: tech.category || fullTechData.category,
      };
    });
  }, []);

  // Récupérer les données du profil
  useEffect(() => {
    const fetchProfileData = async () => {
      if (!username) return;

      setIsLoading(true);
      setError(null);

      try {
        const response = await fetch(`/api/profile/username/${username}`);

        if (!response.ok) {
          throw new Error(
            `Erreur lors de la récupération du profil: ${response.status}`
          );
        }

        const data = await response.json();

        // Hydrater les technologies si elles existent
        if (data.stack && data.stack.technologies) {
          data.stack.technologies = hydrateTechnologies(
            data.stack.technologies
          );
        }

        setProfileData(data);
      } catch (err) {
        console.error("Erreur lors du chargement du profil:", err);
        setError("Impossible de charger ce profil.");
      } finally {
        setIsLoading(false);
      }
    };

    fetchProfileData();
  }, [username, hydrateTechnologies]);

  // Partager le profil
  const handleShareProfile = async () => {
    if (!username) return;

    setIsSharing(true);
    try {
      const shareUrl = `${window.location.origin}/profile/username/${username}`;

      if (navigator.share) {
        await navigator.share({
          title: `${profileData?.user?.name || "Utilisateur"} sur BentoGr.id`,
          text: "Découvrez ma stack technologique sur BentoGr.id!",
          url: shareUrl,
        });
      } else {
        await navigator.clipboard.writeText(shareUrl);
        alert("Lien copié dans le presse-papier!");
      }
    } catch (err) {
      console.error("Erreur lors du partage:", err);
    } finally {
      setIsSharing(false);
    }
  };

  if (isLoading) {
    return <LoadingSkeleton />;
  }

  if (error || !profileData) {
    return (
      <div className="w-full max-w-4xl mx-auto p-4 text-center">
        <h1 className="text-2xl font-bold text-red-500">
          {error || "Profil non trouvé"}
        </h1>
        <p className="mt-4 text-[var(--muted-foreground)]">
          Ce profil n'existe pas ou n'est pas accessible.
        </p>
      </div>
    );
  }

  const { user, stack, hasStripeConnection, stripeAccountMrr } = profileData;

  const ProfileHeader = () => {
    const name = user?.name;
    const image = user?.image;
    const description = user?.description;
    const createdAt = user?.createdAt;
    const shareCount = user?.shareCount || 0;

    return (
      <div className="w-full max-w-4xl mx-auto p-4 md:p-6 bg-[var(--card)] rounded-xl mb-8 border border-[var(--border)] mask-b-from-30%">
        <div className="flex flex-col md:flex-row items-center gap-6">
          {image && (
            <Image
              src={image}
              alt={name || "User avatar"}
              width={128}
              height={128}
              className="rounded-full border-4 border-[var(--primary-foreground)]"
            />
          )}
          <div className="flex-grow text-center md:text-left">
            <h1 className="text-3xl md:text-4xl font-bold text-[var(--text-foreground)] flex items-center gap-3">
              {name || "Anonymous User"}
              {hasStripeConnection && (
                <div className="flex items-center justify-center hidden md:flex ml-3 mt-1 bg-[#635BFF]/40 py-0.5 px-1 rounded-sm">
                  <Stripe className="my-auto" />
                </div>
              )}
            </h1>
            {user?.username && (
              <p className="text-lg text-[var(--muted-foreground)] mt-1">
                @{user.username}
              </p>
            )}
            {description && (
              <p className="text-md text-[var(--muted-foreground)] mt-2">
                {description}
              </p>
            )}
            <div className="flex flex-wrap gap-2 mt-2 justify-center md:justify-start">
              {createdAt && (
                <p className="text-xs text-[var(--muted-foreground)]">
                  Membre depuis: {new Date(createdAt).toLocaleDateString()}
                </p>
              )}
              {shareCount > 0 && (
                <p className="text-xs text-[var(--muted-foreground)]">
                  Profil partagé: {shareCount} fois
                </p>
              )}
            </div>
            <div className="mt-4">
              <Button
                variant="outline"
                size="sm"
                onClick={handleShareProfile}
                disabled={isSharing}
                className="text-[var(--foreground)] hover:bg-[var(--accent)] hover:text-[var(--accent-foreground)]"
              >
                {isSharing ? (
                  <RefreshCw className="mr-2 h-4 w-4 animate-spin" />
                ) : (
                  <Share2 className="mr-2 h-4 w-4" />
                )}
                Partager ce profil
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  };

  const TechCard = ({ tech }: { tech: Tech }) => {
    const isStripeCard = tech.technologyId === STRIPE_CARD_ID;

    return (
      <div
        className={`relative bg-[${
          tech.color || "var(--card)"
        }] rounded-lg p-4 border border-[var(--border)] h-full flex flex-col`}
        style={{
          backgroundColor: tech.color || "var(--card)",
          gridColumn: `span ${tech.gridCols || 1}`,
          gridRow: `span ${tech.gridRows || 1}`,
        }}
      >
        <GlowingEffect className="rounded-lg" />
        <div className="flex justify-between items-start mb-2">
          <div className="flex items-center gap-2">
            {isStripeCard ? (
              <Stripe className="h-6 w-6" />
            ) : tech.logo ? (
              <div
                className="h-6 w-6 flex items-center justify-center"
                dangerouslySetInnerHTML={{ __html: tech.logo }}
              />
            ) : (
              <div className="h-6 w-6 bg-gray-300 rounded-full" />
            )}
            <h3 className="text-sm font-semibold text-[var(--foreground)]">
              {tech.name}
            </h3>
          </div>
          {tech.url && (
            <a
              href={tech.url}
              target="_blank"
              rel="noopener noreferrer"
              className="text-[var(--muted-foreground)] hover:text-[var(--foreground)]"
            >
              <ExternalLink size={16} />
            </a>
          )}
        </div>
        {tech.description && (
          <p className="text-xs text-[var(--muted-foreground)] mb-2">
            {tech.description}
          </p>
        )}
        <div className="mt-auto">
          {isStripeCard && (tech as any).mrr !== undefined && (
            <div className="flex flex-col">
              <span className="text-xs text-[var(--muted-foreground)]">
                MRR Total
              </span>
              <span className="text-lg font-bold text-[var(--foreground)]">
                {(tech as any).mrr.toFixed(2)}{" "}
                {(tech as any).mrrCurrency || "EUR"}
              </span>
            </div>
          )}
          {tech.isProject && (
            <div className="flex gap-3 mt-1">
              {tech.stars !== undefined && (
                <div className="flex items-center gap-1">
                  <span className="text-xs">⭐</span>
                  <span className="text-xs text-[var(--muted-foreground)]">
                    {tech.stars}
                  </span>
                </div>
              )}
              {tech.forks !== undefined && (
                <div className="flex items-center gap-1">
                  <GitHubLogo className="h-3 w-3" />
                  <span className="text-xs text-[var(--muted-foreground)]">
                    {tech.forks}
                  </span>
                </div>
              )}
            </div>
          )}
        </div>
      </div>
    );
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <ProfileHeader />

      {stack && stack.technologies && stack.technologies.length > 0 ? (
        <div className="w-full max-w-4xl mx-auto bg-[var(--card)] p-4 rounded-lg border border-[var(--border)]">
          <h2 className="text-xl font-bold text-[var(--foreground)] mb-4">
            Stack Technologique
          </h2>
          <div
            className="grid gap-4"
            style={{
              gridTemplateColumns: "repeat(auto-fill, minmax(150px, 1fr))",
              gridAutoRows: "minmax(100px, auto)",
            }}
          >
            {stack.technologies.map((tech) => (
              <TechCard key={tech.id} tech={tech} />
            ))}
          </div>
        </div>
      ) : (
        <div className="w-full max-w-4xl mx-auto p-4 text-center">
          <p className="text-[var(--muted-foreground)]">
            Cet utilisateur n'a pas encore ajouté de technologies à sa stack.
          </p>
        </div>
      )}
    </div>
  );
}
